import React, { useState } from 'react';
import Activity from './Activity';
import {Shopee1, Shopee2, Shopee3, Shopee4, Momo1} from './Bulletin';

function TabContent({ brand, activities, isActive }) {
    const [activeActivity, setActiveActivity] = useState(activities[0]);
    console.log(brand,"activeActivity:",activeActivity)
    return isActive ? (
        <div>
            <div className="subtabs">
                {activities.map(activity => (
                    <button
                        key={activity}
                        className={`subtablinks ${activeActivity === activity ? 'active' : ''}`}
                        onClick={() => setActiveActivity(activity)}
                    >
                        {activity}
                    </button>
                ))}
            </div>
            <div className="bulletin-board">
                {/* 根據活動顯示對應的佈告事項 */}
                {activeActivity === '蝦蝦果園澆水' && <Shopee1 />}
                {activeActivity === '每日分享活動' && <Shopee2 />}
                {activeActivity === '檔期活動1' && <Shopee3 />}
                {activeActivity === '檔期活動2' && <Shopee4 />}
                {activeActivity === 'Mo幣簽到分享任務' && <Momo1 />}
            </div>
            {activities.map(activity => (
                <Activity key={activity} brand={brand} activity={activity} isActive={activity === activeActivity} />
            ))}
        </div>
    ) : null;
}

export default TabContent;
