import React, { useState } from 'react';
import TabContent from './components/TabContent';
import Footer from './components/Footer'; // 引入 Footer
import './index.css';

function App() {
    const brands = {
        '蝦皮': ['蝦蝦果園澆水', '每日分享活動', '檔期活動1', '檔期活動2'],
        'Momo': ['Mo幣簽到分享任務']
        // 'Brand3': ['Activity3-1', 'Activity3-2', 'Activity3-3']
    };
    const [activeTab, setActiveTab] = useState('蝦皮');

    return (
        <div className="App">
            <div className="header">
                <img src="./LogoLinkHelper.png" alt="Web Logo" className="brand-logo" width="7%"/>
            </div>
            <div className="body">
                <div className="ad">  </div>
                <div className='mainBody'>
                    <div className="tabs">
                        {Object.keys(brands).map((brand) => (
                            <button
                                key={brand}
                                className={`tablinks ${activeTab === brand ? 'active' : ''}`}
                                onClick={() => setActiveTab(brand)}
                            >
                                {brand}
                            </button>
                        ))}
                    </div>
                    {Object.keys(brands).map((brand) => (
                        <TabContent key={brand} brand={brand} activities={brands[brand]} isActive={activeTab === brand} />
                    ))}
                </div>
                <div className="ad">
                </div>
            </div>
            <Footer /> {/* 添加 Footer */}
        </div>
    );
}

export default App;
